html,
body {
  padding: 0;
  margin: 0;
  height: 100%;
  overflow: hidden;
  -webkit-overflow-scrolling: touch;
}

body {
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

#APP,
#page,
#content,
.ast-container,
#primary,
#main,
article,
.entry-content {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 0;
  margin: 0;
  overflow: hidden;
}

#Dialog {
  position: absolute;
  left: var(--wp--custom--ast-default-block-left-padding);
  bottom: var(--wp--custom--ast-default-block-bottom-padding);
  width: 80%;
  max-width: 500px;
  overflow: hidden;
}
#Dialog .Slide {
  position: absolute;
  bottom: 100%;
  box-sizing: border-box;
}
#Dialog .Slide#Init {
  bottom: 0;
}